<template>
    <div>
        <TopNav mdl="agent" select="1"></TopNav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 1）这里设置分销代理的成为顾问条件以及发展下线条件</p>
        </div>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <div class="wyu-title">
                    <h2>成为顾问条件以及发展下线条件</h2>
                </div>
                <el-form v-model="setting" label-width="150px" style="padding:20px;">
                    <el-form-item label="成为顾问条件">
                        <el-radio-group v-model="setting.condition" @change="agentApply">
                            <el-radio v-for="item in setting.agent_apply_audit" :label="item.id">{{item.name}}</el-radio>
                        </el-radio-group>
                    </el-form-item>
<!--                    <el-form-item label="成为下线条件">-->
<!--                        <el-radio-group v-model="setting.junior">-->
<!--                            <el-radio v-for="item in setting.agent_junior" :label="item.id" >{{item.name}}</el-radio>-->
<!--                        </el-radio-group>-->
<!--                    </el-form-item>-->
                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"  @click="saveForm()">立即保存</el-button>
            <el-button size="small">取消</el-button>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";


    export default {
        name: 'adminRole',
        components: {TopNav},
        data() {
            return {
                loading: false,
                setting: {
                    agent_apply_audit: [],
                    agent_junior: [],
                    condition: 0, //推广的顾问
                    junior: 0,
                }
            }
        },

        created() {
            this.getDetail();
        },

        methods: {
            agentApply() {

            },

            getDetail() {
                this.loading = true;
                api.post("/shop/admin.agent.setting/getApplyJunior", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.setting = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            saveForm() {
                this.loading = true;
                api.post("/shop/admin.agent.setting/setApplyJunior", {
                    appToken: this.$store.getters.appToken,
                    condition: this.setting.condition,
                    shopping_num: this.setting.shopping_num,
                    shopping_money: this.setting.shopping_money,
                    junior: this.setting.junior,
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            }
        }
    }
</script>
